import {useEffect,useState,useRef} from 'react'
import ReactDOM from 'react-dom'
import './symbolInfo.less'
import DeriNumberFormat from '../../../../utils/DeriNumberFormat'
import { firstLetterUppercase } from '../../../../utils/utils'
import Icon from '../../../../components/Icon/Icon'
import classNames from 'classnames'
import ReactTooltip from 'react-tooltip'
import SymbolPanel from './SymbolPanel'
import { inject, observer } from 'mobx-react'
import Tooltip from '../../../../components/Tooltip/Tooltip'

function SymbolInfo({trading,onGirdLayoutChange,lang}){
  const [isShow, setIsShow] = useState(false)
  const toolTipRef = useRef()
  const [isShowKline, setIsShowKline] = useState(false)
  const rateClass = classNames('rate',{
    'up' : trading.symbolInfo.volumn24h > 0,
    'down' : trading.symbolInfo.volumn24h < 0
  })

  const closeTooltip = () => setIsShow(false)

  const calculatePosition = (position,currentEvent,currentTarget,refNode,place,desiredPlace,effect,offset) => {
    const rect = currentTarget.parentElement.getBoundingClientRect();
    const top = rect.top + rect.height;
    return {top:top ,left : rect.left}
  }

  const onClose = () => {
    if(toolTipRef.current){
      toolTipRef.current.tooltipRef = null;
      ReactTooltip.hide();
      setIsShow(false)
    }
  }

  const onLayoutChange = (isMobile) => {
    setIsShowKline(isMobile)
    onGirdLayoutChange(isMobile)
  }
  const closeKline = () => {
    onLayoutChange(false)
  }
  useEffect(() => {
    closeTooltip()
    document.addEventListener('click',closeTooltip)
    window.addEventListener('resize',closeKline)
    return () => {
      document.removeEventListener('click',closeTooltip)
      window.removeEventListener('resize',closeKline)
    }
  }, [trading.symbolInfo,trading.markPrice])
  return (
    <div className='symbol-info' >
      <div className='main'>
        <div className='s-left'  data-for='symbol-panel' data-event='click' data-tip='tooltip'>
          <div>
            <div className='zone'>{firstLetterUppercase(trading.symbolInfo.zone)}</div>
            <div className='symbol-name'><Icon token={trading.symbolInfo.symbol}/>{trading.symbolInfo.symbol}</div>
          </div>
          <div className='select-icon'>
            <Icon token={isShow ? 'arrow-up' : 'arrow-down'} alt='select'/>
          </div>
          <div className='divider-line'></div>
        </div>
        <div className='index-area'>
          <div className='index-area-wraper'>
            <div className={rateClass}>
              {trading.symbolInfo.volumn24h && <Icon token={trading.symbolInfo.volumn24h > 0 ? 'rate-up' : 'rate-down'} alt='' />}
              <DeriNumberFormat value={trading.symbolInfo.volumn24h * 100} decimalScale={2} suffix='%'/></div>
            <div className='index-value'><DeriNumberFormat value={trading.markPrice || trading.symbolInfo.markPrice} decimalScale={2}/></div>
          </div>
        </div>
        <div className='kline-icon' onClick={() => onLayoutChange(!isShowKline)}>
          <Icon token={isShowKline ? 'close-kline' : 'kline'} width={isShowKline ? '16' :'24'} height={isShowKline ? '16' :'24'}/>
        </div>
      </div>
      <Tooltip ref={toolTipRef} afterHide={() => setIsShow(false)} afterShow={() => setIsShow(true)} id='symbol-panel' width={331} place='bottom' overridePosition={calculatePosition}
         resizeHide={false} eventOff={null} scrollHide={false} className='s-react-tooltip' type="info" globalEventOff='click' backgroundColor='#203B60' clickable >
        <SymbolPanel lang={lang} pool={trading.pool} symbols={trading.symbolInfos} onClose={onClose}/>
      </Tooltip>
    </div>
  )
}
export default inject('trading')(observer(SymbolInfo))