import Tab from "../../../../components/Tab/Tab"
import { FUTURE, FUTURE_INNO, FUTURE_OPEN } from "../../../../lib/web3js/shared"
import DeriNumberFormat from "../../../../utils/DeriNumberFormat"
import Arrow from "../../../../components/Arrow/Arrow"
import { ARROW_UP, VERSION_V2, VERSION_V2_LITE, VERSION_V2_OPEN } from "../../../../utils/Constants"
import './symbolPanel.less'
import { inject, observer } from "mobx-react"
import Type from "../../../../model/Type"
import {
  Accordion,
  AccordionItem,
  AccordionItemHeading,
  AccordionItemButton,
  AccordionItemPanel,
} from 'react-accessible-accordion';
import Icon from "../../../../components/Icon/Icon"
import ReactTooltip from "react-tooltip"


function SymbolPanel({ trading, lang, symbols = [] ,onClose }) {
  const onSelect = info => {
    if (info.version === 'v2_lite_open') {
      return;
    }
    trading.onSymbolChange(info)
    onClose && onClose()
  }
  return (
    <div className='symbol-panel'>
      <div className='sp-main'>
        {Type.isFuture ?
          <FuturePanel lang={lang} symbols={symbols} trading={trading} onSelect={onSelect} />
          :
          <OptionPanel symbols={symbols} onSelect={onSelect} />
        }
      </div>
    </div>
  )
}

function FuturePanel({ lang, symbols = [], onSelect }) {
  const tabSet = {
    [lang['main']]: {
      content: <SymbolList lang={lang} symbols={symbols} version={VERSION_V2} onSelect={onSelect} />
    },
    [lang['inno']]: {
      content: <SymbolList lang={lang} symbols={symbols} version={VERSION_V2_LITE} onSelect={onSelect} />
    },
    [lang['open']]: {
      content: <SymbolList lang={lang} symbols={symbols} version={VERSION_V2_OPEN} onSelect={onSelect} />
    }
  }
  return (
    <Tab tabSet={tabSet} activeBgColor='rgba(255, 255, 255, 0.1)' className='future-zone-tab' />
  )
}

const OptionPanel = ({ symbols = {}, onSelect }) => {
  const keys = Object.keys(symbols);
  return (
    <Accordion allowMultipleExpanded allowZeroExpanded preExpanded={[0, 1]} className='option-symbols'>
      {keys.map((key, index) => (
        <AccordionItem uuid={index} key={key}>
          <AccordionItemHeading>
            <AccordionItemButton><Icon token={key} height={30} width={30} style={{ marginRight: '8px' }} />{key}</AccordionItemButton>
          </AccordionItemHeading>
          <AccordionItemPanel>
            {symbols[key].map(info => {
              return (
                <div className='option-row' onClick={e => onSelect(info)}>
                  <div className='option-name'>
                    {info.symbolDisplay}
                  </div>
                  <div className='option-value'>
                    <DeriNumberFormat prefix='$' value={info.markPrice} decimalScale={2} width='56' />
                    {info.volumn24h && <Icon width={8} token={info.volumn24h > 0 ? 'rate-up' : 'rate-down'} />}
                    <DeriNumberFormat value={info.volumn24h * 100} decimalScale={2} suffix='%' />
                  </div>
                </div>
              )
            })}
          </AccordionItemPanel>
        </AccordionItem>
      ))}
    </Accordion>)

}

function SymbolList({ symbols, version, onSelect, lang }) {
  let filterSymbols = symbols.filter(s => s.version === version)
  if (version === 'v2_lite_open') {
    filterSymbols = [
      {
        "symbol": "BTCUSD",
        "version":"v2_lite_open",
        "symbolDisplay": "BTCUSDT/SATS",
      },
      {
        "symbol": "BTCUSD",
        "version":"v2_lite_open",
        "symbolDisplay": "BTCUSDT/ONT",
      },
      {
        "symbol": "ETHUSD",
        "version":"v2_lite_open",
        "symbolDisplay": "ETHUSDT/ONT",
      },
      {
        "symbol": "ONTUSD",
        "version":"v2_lite_open",
        "symbolDisplay": "ONTUSD/ONT",
      },
      {
        "symbol": "BTCUSD",
        "version":"v2_lite_open",
        "symbolDisplay": "BTCUSDT/NULS",
      },
      {
        "symbol": "ETHUSD",
        "symbolDisplay": "ETHUSDT/NULS",
      },
      {
        "symbol": "NULSUSDT",
        "version":"v2_lite_open",
        "symbolDisplay": "NULSUSDT/ONT",
      },
      {
        "symbol": "BTCUSD",
        "version":"v2_lite_open",
        "symbolDisplay": "BTCUSDT/zUSD",
      },
      {
        "symbol": "ETHUSD",
        "version":"v2_lite_open",
        "symbolDisplay": "ETHUSDT/zUSD",
      },
    ]
  }
  return (
    <div className='symbols'>
      {filterSymbols.map((info, index) => {
        return (
          <div className='symbol-row' key={index} onClick={() => onSelect(info)}>
            <div className='ss-name'><Icon token={info.symbol} height={30} width={30} />{info.symbolDisplay}</div>
            <div className='ss-value'>
              <div className='ss-value-detail'>
                {version === "v2_lite_open" && <>
                  <Open lang={lang} />
                </>}
                {version !== VERSION_V2_OPEN && <>
                  <DeriNumberFormat prefix='$' value={info.markPrice} decimalScale={2} width='56' />
                  {info.volumn24h && <Icon width={8} token={info.volumn24h > 0 ? 'rate-up' : 'rate-down'} />}
                  <DeriNumberFormat value={info.volumn24h * 100} decimalScale={2} suffix='%' />
                </>}
              </div>
            </div>
          </div>)
      })}
    </div>
  )
}

function Open({ lang }) {
  return (
    <div className='open-list'>
      {lang['operating']}
    </div>
  )
}

export default inject('trading')(observer(SymbolPanel))