import React,{useState,useEffect} from "react";
import { Pie, PieChart, Cell,Label ,Tooltip} from "recharts";
import styled from "styled-components";
import UnderlineText from "../UnderlineText/UnderlineText";
import { bg } from "../../lib/web3js";
import DeriNumberFormat from "../../utils/DeriNumberFormat";
import { isMobile } from "../../utils/utils";
import ReactTooltip from "../Tooltip/Tooltip";
import './gauge.less'


const Wrapper = styled(UnderlineText)`
  display: flex;
  font-size : 24px;
  color : #E0ECFF;
  position: absolute;
  width :36px;
  justify-content: center;
  left: 46%;
  top: 46%;
  .unit {
    font-size: 12px;
    display: flex;
    margin-left: 2px;
    height: 24px;
    align-items: flex-end;
  }
  .text {
    display : flex;
  }
`

const CustomTooltip = ({ value,tip }) => {
  return (
    <Wrapper tip={tip}>
      <DeriNumberFormat value={value} decaimalScale={0} width='24px'/>
      <div className='unit'> %</div>
    </Wrapper>
  );
};
export default function Gauge({accountInfo = {},lang}){
  const [data, setData] = useState()
  const [coordinate, setCoordinate] = useState({})
  const [endColor, setEndColor] = useState('#FF8C39')
  const [fill, setFill] = useState('#203B60')
  const marginUsagePercent = isNaN(accountInfo.marginUsagePercent) ? '' : accountInfo.marginUsagePercent
  const startPointTip = `${marginUsagePercent}% : ${lang['graph-current-margin-usage-tip']}`
  const Precent100 = lang['graph-100-percent-margin-usage-tip']
  const Precent200 = lang['graph-200-percent-margin-usage-tip']

  useEffect(() => {
    setData([{value : 1}])
    setFill('#203B60')
  }, []);


  useEffect(() => {
    if(accountInfo && accountInfo.marginUsage && accountInfo.dynEffMargin){
      if(accountInfo.marginUsagePercent < 100 ){
        const marginUsage = bg(accountInfo.dynEffMargin).times(accountInfo.marginUsagePercent / 100)
        const data = (+accountInfo.dynEffMargin) === 0 
        if(+accountInfo.dynEffMargin === 0) {
          setData([{value : 1}])
          setFill('#203B60')
        } else {
          setData([
            { value: marginUsage.toNumber()},
            { value: bg(accountInfo.dynEffMargin).minus(marginUsage).toNumber()},
            { value: bg(accountInfo.dynEffMargin).div(2).toNumber()}
          ])
          setCoordinate({
            x1 : '0.5' ,
            y1 : '1',
            x2 : '1',
            y2 : "0.5"
          })
          setFill('url(#usageMargin)')
          setEndColor('#FF8C39');
        }
        
      } else {
        const diff = bg(accountInfo.marginUsagePercent).minus(100).toNumber()
        const overRange = bg(accountInfo.dynEffMargin).div(2)
        const data = [
          { value: bg(accountInfo.dynEffMargin).plus(overRange.times(diff).div(100)).toNumber()},
          { value : 0,},
          { value: overRange.times(100 - diff).div(100).toNumber()}
        ]
        setCoordinate({
          x1 : '0.5' ,
          y1 : '0.5',
          x2 : '1',
          y2 : "0.5"
        })
        setFill('url(#usageMargin)')
        setEndColor('#CE5E5E')
        setData(data)
      }

    }
  }, [accountInfo.marginUsagePercent])
  return (
    <div className='gauge'>
      <PieChart height={150} width={200}>
        <defs>
          <linearGradient
            id="usageMargin"
            {...coordinate}
          >
            <stop offset="0%" stopColor="#8EEC6D" />
            <stop offset="70%" stopColor={endColor} />
          </linearGradient>
          <linearGradient
            id="availMargin"
            gradientTransform="rotate(90deg)"
          >
            <stop offset="0.17%" stopColor="#43608D" />
            <stop offset="100.17%" stopColor="#3F4D8B" />
          </linearGradient>
        </defs>
        <Pie
          cy='55%'
          cx='55%'
          startAngle={225}
          endAngle={-45}
          innerRadius={'60%'}
          outerRadius={'80%'} 
          data={data}
          dataKey="value"
          labelLine={false}
          cornerRadius={5}
          stroke="none"
          isAnimationActive={true}
        >
          <Cell fill={fill}  />
          <Cell fill="url(#availMargin)" fillOpacity='0.4' />
          <Cell fill="#CE5E5E" fillOpacity='0.15'/>
        </Pie>
      </PieChart>
      <CustomTooltip tip={startPointTip} value ={marginUsagePercent}/>
      <ReactTooltip  id='warn-tip' multiline width='375' padding='12'/>
      <div className='start-point'>0%</div>
      <div className='point-100'><UnderlineText  text='100%' tip={Precent100} multiline={true}/></div>
      <div className='point-200'><UnderlineText  text='200%' tip={Precent200} multiline={true}/></div>
      <div className='point-200-red'></div>
    </div>
  )
};