import React, { useState, useEffect } from 'react'
import { inject, observer } from "mobx-react"
import DeriNumberFormat from "../../../../utils/DeriNumberFormat";
import classNames from "classnames";
import './position.less'
import ApiProxy from "../../../../model/ApiProxy";
import {useAlert} from 'react-alert'
import UnderlineText from "../../../../components/UnderlineText/UnderlineText";
import Type from "../../../../model/Type";
import { BUY } from "../../../../utils/Constants";
import styled from "styled-components";
import Button from '../../../../components/Button/Button';

const OptionLiqPrice = styled.div`
  display : flex;
  align-item : center;
`
const LiqText = ({positions, index,lang}) => {
  let ele = <span key={index}>--</span>;
  if (positions.numPositions > 1) {
    if (positions.price1 && positions.price2) {
      ele = <span key={index}>
        <span>{positions.underlier}: </span>
        <span>
          <DeriNumberFormat decimalScale={2} value={positions.price1} />
          <span> / </span>
          <DeriNumberFormat decimalScale={2} value={positions.price2} />
        </span>
      &nbsp;
      </span>
    } else if (!positions.price1 && !positions.price2) {
      ele = <span key={index}>
        <span>{positions.underlier}: </span>
        <span>
          <UnderlineText multiline text='?' tip={lang['liq-price-hover-three']}>  </UnderlineText>
          <span> / </span>
          <UnderlineText multiline  text='?' tip={lang['liq-price-hover-three']} > </UnderlineText>
        </span>
      &nbsp;
      </span>
    } else if (!positions.price1 && positions.price2) {
      ele =
        <span key={index}>
          <span>{positions.underlier}: </span>
          <span>
            <UnderlineText  tip={lang['liq-price-hover-three']} text='?' >    </UnderlineText>
            <span> / </span>
            <span> <DeriNumberFormat decimalScale={2} value={positions.price2} /> </span>
          </span>
      &nbsp;
      </span>
    } else if (positions.price1 && !positions.price2) {
      ele = <span key={index}>
        <span>{positions.underlier}: </span>
        <span>
          <span> <DeriNumberFormat decimalScale={2} value={positions.price1} /> </span>
          <span> / </span>
          <UnderlineText tip={lang['liq-price-hover-three']} text='?' > </UnderlineText>
        </span>
      &nbsp;
      </span>
    }
    return ele
  } else {
    if (positions.price1 && positions.price2) {
      ele = <span key={index}>
        <span>{positions.underlier}: </span>
        <span>
          <DeriNumberFormat decimalScale={2} value={positions.price1} />
          <span> / </span>
          <DeriNumberFormat decimalScale={2} value={positions.price2} />
        </span>
      &nbsp;
      </span>
    } else if (!positions.price1 && !positions.price2) {
      ele = <span key={index}>
        <span>{positions.underlier}: </span>
        <span>
          <UnderlineText tip={lang['liq-price-hover-two']} text='--' ></UnderlineText>
          <span> / </span>
          <UnderlineText tip={lang['liq-price-hover-two']} text='--' >  </UnderlineText>
        </span>
      &nbsp;
      </span>
    } else if (!positions.price1 && positions.price2) {
      ele =
        <span key={index}>
          <span>{positions.underlier}: </span>
          <span>
            <UnderlineText  tip={lang['liq-price-hover-two']} text='--' > </UnderlineText>
            <span> / </span>
            <span> <DeriNumberFormat decimalScale={2} value={positions.price2} /> </span>
          </span>
      &nbsp;
      </span>

    } else if (positions.price1 && !positions.price2) {
      ele = <span key={index}>
        <span>{positions.underlier}: </span>
        <span>
          <span> <DeriNumberFormat decimalScale={2} value={positions.price1} /> </span>
          <span> / </span>
          <UnderlineText tip={lang['liq-price-hover-two']} text='--'>   </UnderlineText>
        </span>
      &nbsp;
      </span>
    }
    return ele
  }
}

const LiqPriceDisplay = ({pos,lang}) => {
  const liqPrices = pos.liquidationPrice;
  return (
    Array.isArray(liqPrices) 
    ? 
      liqPrices.map((detail,index) => <LiqText positions={detail} index={index} key={index} lang={lang}/>)
    : <DeriNumberFormat value={liqPrices} decimalScale={2} />
  )
}

const formatNumber = (number,decimal = 2,suffix = '',prefix ='') => {
  return <DeriNumberFormat value={number} decimalScale={decimal} prefix={prefix} suffix={` ${suffix}`}/>
}


function Position({wallet,lang,trading}){
  const alert = useAlert();
  let columns = {
    symbol : {text : lang['symbol']},
    direction : {text : lang['direction'],className : pos => pos.directionColor},
    position : {text : lang['position'],value : pos => <><div>{formatNumber(pos['volume'],trading.minTradeScaleDecimal,pos.symbolUnit)}</div><div className='gray'>{formatNumber(pos['notional'],2,'USD')}</div></>},
    averageEntryPrice : {text : lang['entry-price'],value : pos => formatNumber(pos['averageEntryPrice'],2)},
    markPrice : {text : lang['mark-price'],value : pos => formatNumber(pos['markPrice'])},
    marginHeldBySymbol : {text : lang['margin-usage'],value : pos => formatNumber(pos['marginHeldBySymbol'])},
    liquidationPrice :  {text : lang['liq-Price'],value : pos => formatNumber(pos['liquidationPrice'],2)},
    unrealizedPnl : {text : lang['unrealizedPnl'],className : pos => pos['unrealizedPnl'] > 0 ? 'green' : 'red',value : pos => <><div>{formatNumber(pos['unrealizedPnl'])}</div><div>{formatNumber(pos['unrealizedPnlRate'],2,'%')}</div></>},
    fundingFee : {text : lang['fundingFee'],className : pos => (+pos['fundingFee']) > 0 ? 'green' : 'red',tip: lang['funding-fee-tip'],value : pos => formatNumber((pos['fundingFee']),8)},
    action : {text : lang['action'],value  : pos => closeButton(pos.symbolId,lang['close']),className : 'close-position'}
  }
  if(Type.isOption){
    columns = {
      symbol : {text : lang['symbol']},
      direction : {text : lang['direction'],className : pos => pos.directionColor},
      position : {text : lang['position'],value : pos => <><div>{formatNumber(pos['volume'],trading.minTradeScaleDecimal,pos.symbolUnit)}</div><div className='gray'>{formatNumber(pos['notional'],2,'USD')}</div></>},
      averageEntryPrice : {text : lang['entry-price'],value : pos => formatNumber(pos['averageEntryPrice'],2)},
      markPrice : {text : lang['mark-price'],value : pos => formatNumber(pos['markPrice'])},
      marginHeldBySymbol : {text : lang['margin-usage'],value : pos => formatNumber(pos['marginHeldBySymbol'])},
      unrealizedPnl : {text : lang['unrealizedPnl'],className : pos => pos['unrealizedPnl'] > 0 ? 'green' : 'red',value : pos => <><div>{formatNumber(pos['unrealizedPnl'])}</div><div>{formatNumber(pos['unrealizedPnlRate'],2,'%')}</div></>},
      fundingFee : {text : lang['fundingFee'],className : pos => (+pos['fundingFee']) > 0 ? 'green' : 'red',tip: lang['funding-fee-tip'],value : pos =>  formatNumber((pos['fundingFee']),8)},
      action : {text : lang['action'],value  : pos => closeButton(pos.symbolId,lang['close']),className : 'close-position'}
    }
  }
  const keys = Object.keys(columns)
  
  const closePosition = async symbolId => {
    const res = await ApiProxy.request('closePosition',
      [wallet.detail.chainId, trading.pool.address, wallet.detail.account, symbolId],
      {includeResponse : true})
    if(res.success){
      alert.success(lang['close-position-success']);
      trading.refresh();
    } else {
      alert.error(lang['close-position-failed'])
    }
  }
  const closeButton = (symbolId,label) => {
    return  <Button label={label} bgColor= '#0A172B' defaultBorderColor='rgba(205,122,55,0.5)' outline outlineColor='rgb(205,122,55)' onClick={() => closePosition(symbolId)} width='60' height='32'/>
  }

  return (
    <div className='m-position'>
     {Type.isOption && <div className='option-liq-price'>
        <span>
          <UnderlineText multiline tip= {lang["option-liq-price-tip"]} text={lang['liq-Price']} />
        </span>
        <span> : {trading.positions[0] ? <LiqPriceDisplay pos={trading.positions[0]} lang={lang}/> : ''} 
         </span>
      </div>}
      <div className='p-header'>
        {keys.map(key => {
          return (
            <div key={key} className ='p-col'>
              {columns[key].tip 
              ? 
                <UnderlineText tip={columns[key].tip} text={columns[key].text} multiline width='auto'/> 
              :
               columns[key].text
              }
            </div>
          )
        })}
      </div>
      <div className='p-body'>
        {trading.loaded ?
          trading.positions.map((position,i) => (
            <div className='p-row' key={i}>
              {keys.map(key => {
                const col = columns[key]
                const colClass = classNames('p-col',typeof col.className === 'function' ? col.className(position) : col.className)
                return (
                  <div className={colClass} key={key} onClick={() => typeof col.onClick === 'function' && col.onClick(position)}>
                    {typeof col.value === 'function' ? col.value(position) : (position[key] || columns[key].value)}
                  </div>
                )
              })}
            </div>
          ))
        : <>
          <div className='p-row loading-line'></div><div className='p-row loading-line'></div><div className='p-row loading-line'></div><div className='p-row loading-line'></div><div className='p-row loading-line'></div>
        </>
      }
        
        
      </div>
    </div>
  )
}

export default inject('trading','wallet')(observer(Position))